<script>
import Header from "@/components/Header2/header2.vue";
import Footer from "@/components/Footer/footer.vue";
import UploadComponent from "@/components/Upload/Upload.vue";
import configSetting from "../../../../public/config/customSetting.json";

export default {
  name: "companyApplyAccount-component",
  components: {
    Header,
    Footer,
    UploadComponent,
  },
  data() {
    return {
      active: 0,
      footerHeight: 0,
      fixedBtnGroupHeight: 0,
      formData: {
        S_ID: "",
        S_NAME: "",
        PASSWORD: "",
        SHORT_NAME: "",
        COMPANY_HEAD_NAME: "",
        TEL: "",
        ADDRESS1: "",
        ADDRESS2: null,
        EMAIL: "",
        COUNTRY: "",
        P_NAME: "",
        SEX: "",
        ADMIN_ID: "",
        DEPT_NAME: "",
        JOB_TITLE: "",
        URGENT_TEL: "",
        PEMAIL: "",
        file: {},
      },
      rules: {
        S_ID: [{ required: true, message: "必填", trigger: "blur" }],
        PASSWORD: [
          { required: true, message: "必填", trigger: "blur" },
          { min: 8, message: "至少8碼", trigger: "blur" },
          { validator: this.validateRule, trigger: "blur" },
        ],
        S_NAME: [
          { required: true, message: "必填", trigger: "blur" },
          { max: 30, message: "不可超過30個字", trigger: "blur" },
        ],
        SHORT_NAME: [
          { required: true, message: "必填", trigger: "blur" },
          { max: 4, message: "不可超過4個字", trigger: "blur" },
        ],
        COMPANY_HEAD_NAME: [
          { required: true, message: "必填", trigger: "blur" },
        ],
        TEL: [{ required: true, message: "必填", trigger: "blur" }],
        COUNTRY: [{ required: true, message: "必填", trigger: "change" }],
        ADDRESS1: [{ required: true, message: "必填", trigger: "blur" }],
        EMAIL: [
          { required: true, message: "必填", trigger: "blur" },
          {
            type: "email",
            message: "信箱格式錯誤",
            trigger: ["blur", "change"],
          },
        ],
        file: [{ required: true, message: "必填", trigger: "change" }],
        P_NAME: [{ required: true, message: "必填", trigger: "blur" }],
        SEX: [{ required: true, message: "必填", trigger: "change" }],
        ADMIN_ID: [{ required: true, message: "必填", trigger: "blur" }],
        DEPT_NAME: [{ required: true, message: "必填", trigger: "blur" }],
        JOB_TITLE: [{ required: true, message: "必填", trigger: "blur" }],
        URGENT_TEL: [{ required: true, message: "必填", trigger: "blur" }],
        PEMAIL: [{ required: true, message: "必填", trigger: "blur" }],
      },
    };
  },
  computed: {
    newVendorInfo() {
      this.$store.state.newVendorInfo.forEach((e) => {
        e.label = `${e.name} ${e.e_Name}`;
      });
      return this.$store.state.newVendorInfo;
    },
  },
  methods: {
    getInitToken() {
      return new Promise((resolve, reject) => {
        localStorage.clear();
        let p4use = {};
        let acnt = "account";
        let v1 = "12800225;evendor_support@innolux.com";
        let pswd = "password";
        let v2 = "1QaX2WsX";
        let apid = "app_id";
        let v3 = configSetting.APP_ID;

        p4use[acnt] = v1;
        p4use[pswd] = v2;
        p4use[apid] = v3;
        let params = p4use;
        this.axios({
          url: configSetting.APIM_GATEWAY + configSetting.TWOFALOGIN_METHOD,
          method: "POST",
          data: params,
          headers: {
            "content-type": "application/json",
          },
        })
          .then((res) => {
            resolve(res);
          })
          .catch((err) => {
            reject(err);
          });
      });
    },
    fileChangeHandler(fileList) {
      this.formData.file = fileList.raw;
      this.$refs.form.clearValidate("file");
    },
    fileRemoveHandler(fileList) {
      this.formData.file = fileList;
      this.$refs.form.validateField("file");
    },
    submitForm(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          localStorage.clear();
          let p4use = {};
          let acnt = "account";
          let v1 = "12800225;evendor_support@innolux.com";
          let pswd = "password";
          let v2 = "1QaX2WsX";
          let apid = "app_id";
          let v3 = configSetting.APP_ID;

          p4use[acnt] = v1;
          p4use[pswd] = v2;
          p4use[apid] = v3;
          let params = p4use;
          this.axios({
            url: configSetting.APIM_GATEWAY + configSetting.TWOFALOGIN_METHOD,
            method: "POST",
            data: params,
            headers: {
              "content-type": "application/json",
            },
          }).then(() => {
            this.addRegister();
          });
        } else {
          this.$message({
            type: "error",
            showClose: true,
            message: "資料輸入錯誤",
          });
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    validateRule(rule, value, callback) {
      const reg = /^(?=.*\d)(?=.*[a-zA-Z])(?=.*[~!@#$%^&*])[\da-zA-Z~!@#$%^&*]/;
      if (!reg.test(value)) {
        callback(new Error("必須包含數字/符號/英文大小寫混合"));
      } else {
        callback();
      }
    },
    async addRegister() {
      try {
        await this.$store.dispatch("addRegister", this.formData);
        this.$message({
          type: "success",
          duration: 5000,
          showClose: true,
          message: "註冊成功",
        });
        this.$router.replace({ name: "index" });
      } catch (err) {
        console.log(err);
      }
    },
    async getNewVendorInfo() {
      try {
        await this.$store.dispatch("getNewVendorInfo");
      } catch (err) {
        console.log(err);
      }
    },
    async downloadHandler() {
      try {
        await this.getInitToken();
        const res = await this.$store.dispatch("getDownloadTerms");
        const href = URL.createObjectURL(res.data);
        const link = document.createElement("a");
        link.href = href;
        link.setAttribute("download", "群創光電承攬商約定條款.docx");
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
        URL.revokeObjectURL(href);
      } catch (err) {
        console.log(err);
      }
    },
  },
  async mounted() {
    try {
      await this.getInitToken();
    } catch (err) {
      console.log(err);
    }
    this.getNewVendorInfo();
    this.$root.$children[0].sidebarSwitch = false;
    this.$store.commit("HEADER_HANDLER", false);
    this.$store.commit("BREADCRUMB_HANDLER", false);

    const footer = document.querySelector(".footer");
    const fixedBtnGroup = document.querySelector(".fixed-btn-group");
    if (footer && fixedBtnGroup) {
      this.footerHeight = footer.offsetHeight;
      this.fixedBtnGroupHeight = fixedBtnGroup.offsetHeight;
    }
  },
  beforeDestroy() {
    localStorage.clear();
  },
};
</script>

<template src="./companyApplyAccountStep2.html"></template>
<style lang="sass" scoped src="./companyApplyAccountStep2.sass"></style>
